		downLoad(uri) {
		   
		    var that = this;		  
			var temp = uri.split("/");
			var fileName =temp[temp.length-1];
		    wx.downloadFile({
		      url: uri,
		      success: function(res) {
		        const manage = wx.getFileSystemManager();
		        if (res.statusCode === 200) {
				  var tempFilePath = res.tempFilePath;	
				  var dir=new Date().getTime();
				  	manage.mkdir({
				  		dirPath:wx.env.USER_DATA_PATH + "/"+dir,
						recursive: false,
				  		success(res){
				  			
							manage.saveFile({
							  tempFilePath: tempFilePath,
							  filePath: wx.env.USER_DATA_PATH + "/"+dir +"/"+ fileName,
							  success: function(res) {
								  uni.showToast({
									  icon:"none",
									  title:"下载成功"
								  });
							  },
		    				fail(res){
								uni.showToast({
									icon:"none",
									title:"下载失败,请重试"
								});
							}
							});
							
							
							
				  		},
				  		fail(res){
				  			uni.showToast({
				  				icon:"none",
				  				title:"下载失败,请重试"
				  			});
				  		}
				  	});

		        }else{
					uni.showToast({
						icon:"none",
						title:"下载失败,请重试"
					});
				}
				
				
				
				
		       
		      }
		    })
		  },
		  
		  test2()
		  {
			 let uri = "http://cn.createpdfonline.org/pdffiles/hello%20word(20200623144016).pdf";
			 
			 var temp = uri.split("/");
			 if(temp.length>0){
			 console.log(temp[temp.length-1]);
			 }
			 //var fileName = url.split('/').length-1
		  },
		  //根据时间戳转成时间
		 
		      timestamp(timestamp) {
		          // formats格式包括
		          // 1. Y-m-d
		          // 2. Y-m-d H:i:s
		          // 3. Y年m月d日
		          // 4. Y年m月d日 H时i分
				  var formats="Y-m-d H:i:s";
		          formats = formats || 'Y-m-d';
		      
		          var zero = function (value) {
		              if (value < 10) {
		                  return '0' + value;
		              }
		              return value;
		          };
		      
		          var myDate = timestamp? new Date(parseInt(timestamp)): new Date();
		      
		          var year = myDate.getFullYear();
		          var month = zero(myDate.getMonth() + 1);
		          var day = zero(myDate.getDate());
		      
		          var hour = zero(myDate.getHours());
		          var minite = zero(myDate.getMinutes());
		          var second = zero(myDate.getSeconds());
				  //return year+"-"+month+"-"+day+" "+hour+":"+minite+":"+second;
		      
		         return formats.replace(/Y|m|d|H|i|s/ig, function (matches) {
		              return ({
		                  Y: year,
		                  m: month,
		                  d: day,
		                  H: hour,
		                  i: minite,
		                  s: second
		              })[matches];
		          });
		      }
		    


		
	}
}
